<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<blockquote>
<p>有默认样式，距离底部 <code v-pre="">50px</code>，可覆盖。</p>
</blockquote>
<blockquote>
<p>自定义样式宽高不大于 40px * 40px。</p>
</blockquote>
<h3 id="backtop-props"><a class="header-anchor" href="#backtop-props" aria-hidden="true">¶</a> BackTop Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>visibilityHeight</td>
<td>滚动高度达到此参数值才出现 <code v-pre="">BackTop</code></td>
